.ruler-horizontal, .ruler-vertical {
    position: fixed;
    background-color: gray;
    z-index: 1000;
    color: black;
    font-size: 12px;
  }
  
  .ruler-horizontal {
    width: 100%;
    height: 20px;
    top: 0;
    text-align: right;
  }
  
  .ruler-vertical {
    height: 100%;
    width: 20px;
    left: 0;
    top: 0;
    writing-mode: vertical-lr;
    text-align: center;
  }

  .tick {
    position: absolute;
    background-color: #333;
  }
  
  .ruler-horizontal .tick {
    width: 1px;
    height: 10px;
  }
  
  .ruler-vertical .tick {
    width: 10px;
    height: 1px;
  }

  .mouse-line-horizontal, .mouse-line-vertical {
    position: fixed;
    background-color: rgb(156, 156, 156); /* 선의 색상 */
    z-index: 1001; /* 눈금자 위에 표시 */
  }
  
  .mouse-line-horizontal {
    left: 0;
    width: 100%;
    height: 1px; /* 수평선의 두께 */
  }
  
  .mouse-line-vertical {
    top: 0;
    height: 100%;
    width: 1px; /* 수직선의 두께 */
  }

  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    grid-auto-rows: 100px;
    gap: 10px;
    height: 100vh;
    overflow: auto;
  }

  
  .current-square-info {
    position: fixed;
    top: 0;
    right: 0;
    background-color: #eee;
    padding: 8px;
    z-index: 10;
  }
  
  .square-container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 0px; /* 상단 눈금자의 높이만큼 오프셋 */
    margin-left: 0px; /* 좌측 눈금자의 너비만큼 오프셋 */
    // width: 1090px;
  }
  
  .square {
    border: 1px solid #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    transition: width 0.2s ease-in-out, height 0.2s ease-in-out; /* 부드러운 변화 효과 */
    color: #eee;
  }

  /* square.component.css */
.selected {
  background-color: #f00; /* 선택된 사각형의 배경색 */
}

.drag-box {
  position: absolute;
  border: 2px dashed #000;
  z-index: 1000;
  pointer-events: none; /* 선택 박스가 마우스 이벤트를 방해하지 않도록 설정 */
}


.mat-drawer-close button[mat-icon-button] { /* 닫기 버튼 스타일 */
  position: absolute;
  top: 10px; /* 상단 여백 조절 */
  right: 10px; /* 우측 여백 조절 */
  opacity: 0.5;
}

.mat-drawer { /* mat-drawer에 직접 스타일 적용 */
  background: rgba(255, 255, 255, 0.25); /* 배경 투명도 및 색상 */
  backdrop-filter: blur(10px); /* 배경 블러 */
  border-radius: 10px; /* 둥근 모서리 (필요에 따라 조절) */
  border: 1px solid rgba(255, 255, 255, 0.5); /* 밝은 테두리 */
}

.info {
  position: sticky;
  top: 0;
  z-index: 3;
  height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toggle-container {
  display: flex; 
  align-items: center;
  gap: 10px; /* 간격 조절 */
}

.info-text-container {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.squares-key {
  color: orange; /* Squares: 파란색 */
  margin-right: 10px;
}

.squares-message {
  color: rgb(91, 91, 91); /* Squares: 파란색 */
  margin-right: 10px;
}

.squares-length {
  color: black; /* Squares: 파란색 */
}

.squares-length-value {
  color: rgb(43, 43, 43); /* Squares 값: 초록색 */
}

.current-square-index {
  color: red; /* currentSquareIndex: 빨간색 */
  font-weight: bold;
}

.dragged-columns {
  color: gray; /* draggedColumns: 보라색 */
}

.dragged-rows {
  color: gray; /* draggedRows: 주황색 */
}

.selected-indices {
  color: gray(108, 210, 241); /* selectedIndices: 갈색 */
}

mat-drawer-container {
  display: flex; /* Use flexbox for easy centering */
  justify-content: center; /* Horizontally center the content */
}


